import React, { useState, useRef, useEffect } from "react";
import wrapper from "../../commons/WebComponentWrapper";
import { Accordion, Form } from "carbon-components-react";
import { EventAPI } from "../../commons/EventAPI";

const dispatchedEvents = [];

/*
    Example: 
        <srs-form data={} sections=[{name: "section1", title: "Section 1", columns: 2}, {name: "section2", title: "Section 2", columns: 4}]>
            <srs-form-section name="section1" columns="2">
                <srs-form-field type="input" value_changed_event="" name="field1" field_data={}/>
                <srs-form-field type="input" name="field2" field_data={}/>
                <srs-form-field type="input" name="field3" field_data={}/>
            </srs-form-section>
            <srs-form-section name="section2" columns="4">
                ...
            </srs-form-section>
        </srs-form>
*/
const SrsForm = function (props, ref) {
    const [formValue, setFormValue] = useState(props.data || {});
    const sectionRef = useRef(null);
    useEffect(() => {
        const currentNode = ref.current;
        if (!currentNode) return;

        if (ref.current) {
            ref.current.addEventListener(EventAPI.FORM_FIELD_VALUE_CHANGED, (evt) => {
                let currentFormData = { ...formValue };
                currentFormData[evt.detail.payload.fieldName] = currentFormData[evt.detail.payload.fieldName] || {};
                currentFormData[evt.detail.payload.fieldName].value = evt.detail.payload.value;
                setFormValue(currentFormData);
                props.dispatchEvent(EventAPI.FORM_VALUE_CHANGED, { payload: { value: currentFormData, id: props.elementid } });
                props.setValue(currentFormData);
                if (sectionRef.current) {
                    let assignedElements = sectionRef.current.assignedElements();
                    for (let i = 0; i < assignedElements.length; i++) {
                        assignedElements[i].form_data = JSON.stringify(currentFormData)
                    }
                }
            });
        }

        if (sectionRef.current) {
            let assignedElements = sectionRef.current.assignedElements();
            for (let i = 0; i < assignedElements.length; i++) {
                assignedElements[i].setAttribute('form_data', JSON.stringify(formValue));
            }
        }

    }, [props.name, props.data]);


    return (<Form>
        <Accordion>
            <slot ref={sectionRef}></slot>
        </Accordion>
    </Form>)
}

const styles = `

`
const props = [{
    name: "name",
    defaultValue: "",
    type: "string"
}, {
    name: "data",
    defaultValue: {},
    type: "object"
}]

const slots = [{name: "default", allowed: ["srs-form-section"]}];

const WrappedComponent = wrapper(React.forwardRef(SrsForm), props);
export { WrappedComponent, styles, props, dispatchedEvents, slots };